<template>
    <div id="contact-bar" class="contact-bar">
        <div class="row" style="justify-content: space-between">
            <div class="left-side row" style="gap: 70px;">
                <div class="row language">
                    <b-dropdown text="Language" dropdown>
                        <template #button-content>
                            <b-icon icon="globe" style="margin-right: 5px;"></b-icon>
                            Language
                            <b-icon icon="chevron-down"></b-icon>
                        </template>
                        <b-dropdown-item>Arabic</b-dropdown-item>
                        <b-dropdown-item>English</b-dropdown-item>
                    </b-dropdown>
                </div>
                |
                <div class="row" style="gap: 20px;">
                    <div class="social-icon">
                        <b-icon icon="facebook"></b-icon>
                    </div>
                    <div class="social-icon">
                        <b-icon icon="instagram"></b-icon>
                    </div>
                    <div class="social-icon">
                        <b-icon icon="linkedin"></b-icon>
                    </div>
                </div>
            </div>
            <div class="right-side row">
                <div class="row" style="gap: 10px;">
                    <p>+963 989 202 089</p>
                    <b-icon icon="whatsapp"></b-icon>
                </div>
                <div class="row" style="gap: 10px;">
                    | <p>+963 989 202 090</p>
                    <b-icon class="icon-contact" icon="telephone-fill" aria-hidden="true"></b-icon>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ContcatBar",
    data() {
        return {
            en: 'English',
            ar: '',
            lang: '',
            screenWidth: 0,
        };
    },
    computed: {
        isAr() {
            this.lang = localStorage.getItem("lang")
            return this.lang == 'ar'
        },
    },
    methods: {
        onScreenResize() {
            window.addEventListener("resize", () => {
                this.updateScreenWidth();
            });
        },
        updateScreenWidth() {
            this.screenWidth = window.innerWidth;
        },
    },
    mounted() {
        this.updateScreenWidth();
        this.onScreenResize();
    },
};

</script>


<style lang="scss">
.contact-bar {
    color: #fff;
    padding: 20px 70px 0px 120px;
    border-bottom: 0.1px solid #ffffff7a;
    z-index: 10000;
    position: relative;
}

.contact-bar .right-side {
    gap: 50px;
}

.contact-bar .bi-globe {
    font-size: 17px !important;
}

.contact-bar .b-icon {
    margin-top: 5px;
    cursor: pointer;
    margin-bottom: 2px;
}

.contact-bar .social-icon:hover {
    cursor: pointer;
    font-size: 19px;
    transition: all .3s;
}

.contact-bar .social-icon {
    transition: all .3s;
}

.contact-bar .bi-chevron-down {
    font-size: 15px !important;
}

.contact-bar .dropdown-item.active,
.dropdown-item:active {
    color: #fff;
    text-decoration: none;
    background-color: var(--main-blue) !important;
}

.contact-bar .dropdown {
    top: -10px;
    right: 40px;
}
</style>
