<template>
    <section id="job-form" class="job-form">
        <div class="container">
            <b-form>
                <div class="container-form">
                    <h3 class="tap-name">
                        Personal Info
                    </h3>
                    <div class="line"></div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="name" label="Full name" label-for="name">
