                                <div class="error-section" v-if="v$.name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="nationality" label="Nationality" label-for="nationality">
                                <b-form-input id="nationality" v-model="nationality"
                                    placeholder="Nationality"></b-form-input>
                                <div class="error-section" v-if="v$.nationality.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.nationality.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group label="Gender" label-for="gender">
