                            <b-form-group id="find" label="How did you find out about our job opening?" label-for="find">
                                <select v-model="find" class="my-select">
                                    <option disabled value="" class="my-option">Please select one</option>
                                    <option v-for="(item, index) in find_option" :key="index">
                                        {{ item }}
                                    </option>
                                </select>
                                <div class="error-section" v-if="v$.find.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.find.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
