                        <div class="col-lg-6">
                            <b-form-group id="hobbies" label="Hobbies" label-for="hobbies">
                                <b-form-input id="hobbies" v-model="hobbies" placeholder="Hobbies"></b-form-input>
                                <div class="error-section" v-if="v$.hobbies.$error">
                                    <b-icon icon="exclamation-triangle">
