                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="cover_letter" label="Cover letter" label-for="cover_letter">
                                <b-form-textarea id="cover_letter" v-model="cover_letter" placeholder="Your cover letter..."
                                    style="height:100px"></b-form-textarea>
                                <div class="error-section" v-if="v$.cover_letter.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.cover_letter.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <h3 class="tap-name" style="margin-top: 40px">
