                    </h3>
                    <div class="line"></div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group label="Do you have health problems?" label-for="health">
                                <b-form-radio-group v-model="health" :options="health_option"></b-form-radio-group>
                                <div class="error-section" v-if="v$.health.$error">
