                                    </b-icon>
                                    <span class="error">{{ v$.health.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group label="Do you smoke?" label-for="smoke">
                                <b-form-radio-group v-model="smoke" :options="smoke_option"></b-form-radio-group>
                                <div class="error-section" v-if="v$.smoke.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.smoke.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <h3 class="tap-name" style="margin-top: 40px">
                        Attachments
                    </h3>
                    <div class="line"></div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="cv" label="CV" label-for="cv">
                                <b-form-file @change="onCvSelected" v-model="cv"></b-form-file>
                                <div class="error-section" v-if="v$.cv.$error">
