                                    </b-icon>
                                    <span class="error">{{ v$.cv.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="qualification" label="Qualification" label-for="qualification">
                                <b-form-file @change="onQualificationSelected" v-model="qualification"></b-form-file>
                                <div class="error-section" v-if="v$.qualification.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.qualification.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="courses" label="Courses" label-for="courses">
                                <b-form-file @change="onCoursesSelected" v-model="courses"></b-form-file>
                                <div class="error-section" v-if="v$.courses.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.courses.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <!-- <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="project_details" label="Project Details" label-for="project_details">
                                <b-form-textarea id="project_details" v-model="project_details"
                                    placeholder="Tell us about your project " style="height:100px"></b-form-textarea>
                                <div class="error-section" v-if="v$.project_details.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.project_details.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="file" label="Project Details" label-for="file">
                                <b-form-file @change="onFileSelected" v-model="file"></b-form-file>
                                <div class="error-section" v-if="v$.file.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.file.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div> -->
                    <!-- <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group label="Project type" label-for="project_type">
                                <b-form-checkbox-group v-model="project_type"
                                    :options="project_type_option"></b-form-checkbox-group>
                                <div class="error-section" v-if="v$.project_type.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.project_type.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div> -->
                    <div class="line"></div>
                    <div style="text-align:justify">
                        <button v-on:click="submitForm" type="button" class="my-button"> Submit</button>
                    </div>
                </div>
            </b-form>
        </div>
    </section>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, helpers, minLength } from '@vuelidate/validators'


export default {
    name: "jobForm",
    setup() {
        return { v$: useVuelidate() }
    },
    components: {
    },
    data() {
        return {
            name: '',
            birth_date: '',
            gender: '',
            gender_option: ['Male', 'Female'],
            nationality: '',
            driving_license_type: '',
            matrial_status: '',
            matrial_status_option: ['Single', 'Engaged', 'Married', 'Separated', 'Widowed'],
            driving_license_type: '',
            phone_number: '',
            mobile_number: '',
            home_address: '',
            job_name: '',
            job_name_option: ['Front End', 'Back End', 'Grphic Design', 'Data Entry', 'Marketing', 'UI/UX Design'],
            shift: '',
            shift_option: ['Remote', 'Part Time', 'Full Time'],
            possible_start_date: '',
            expected_salary: '',
            reasons: '',
            last_job: '',
            find: '',
            find_option: ['From friend', 'Social media', 'Google', 'Other...'],
            hobbies: '',
            cover_letter: '',
            health: '',
            health_option: ['Yes', 'No'],
            smoke: '',
            smoke_option: ['Yes', 'No'],
            cv: '',
            courses: '',
            qualification: '',
        };
    },
    validations() {
        return {
            name: {
                required: helpers.withMessage('This field is required', required)
            },
            birth_date: {
                required: helpers.withMessage('This field is required', required)
            },
            nationality: {
                required: helpers.withMessage('This field is required', required)
            },
            gender: {
                required: helpers.withMessage('This field is required', required)
            },
            driving_license_type: {
                required: helpers.withMessage('This field is required', required)
            },
            matrial_status: {
                required: helpers.withMessage('This field is required', required)
            },
            driving_license_type: {
                required: helpers.withMessage('This field is required', required)
            },
            phone_number: {
                required: helpers.withMessage('This field is required', required),
                minLength: helpers.withMessage('Please enter valid phone number', minLength(10))
            },
            mobile_number: {},
            home_address: {},
            job_name: {},
            shift: {},
            possible_start_date: {},
            expected_salary: {},
            reasons: {},
            last_job: {},
            find: {},
            hobbies: {},
            cover_letter: {},
            health: {},
            smoke: {},
            cv: {},
            courses: {},
            qualification: {}
        }
    },
    methods: {
        submitForm() {
            // console.log("Sssssssss");
            this.v$.$validate();
            if (!this.v$.$error) {
                console.log("TRRRRRRUUUUEEEEE")
            }
            // console.log(this.v$.$error)
        },
        onCvSelected(files) {
            this.cv = files.target.files[0];
        },
        onCourserSelected(files) {
            this.courses = files.target.files[0];
        },
        onQualificationSelected(files) {
            this.qualification = files.target.files[0];
        },
    }
};
</script>

<style lang="scss">
.job-form {
    padding: 50px
}

.job-form .form-group {
    justify-content: space-between;
    margin: 20px 0px;
}

.job-form .container-form {
    margin-right: 4%;
    margin-left: 4%;
    margin-top: 30px;
}

.job-form .form-control,
.job-form .custom-file {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 10px;
    background-color: #bebebe11;
    transition: all 0.5s ease;
}

.job-form .form-control:focus {
    box-shadow: none;
    border: 1px solid var(--main-blue);
    background: #fff;
    transition: all 0.5s ease;
    box-shadow: 0px 0px 6px #24487961;
}

.job-form .d-block {
    color: var(--main-blue);
    text-align: justify;
    font-size: 18px;
}

.job-form .line {
    border-bottom: 1px solid #bebebe;
    margin-top: 20px;
}

.job-form .tap-name {
    text-align: justify;
    color: var(--main-blue);
    font-weight: 800;
}

@media (min-width: 992px) {
    .job-form .my-row {
        margin-top: 20px;
    }
}

.job-form .error {
    color: #b61532;
    margin: 5px;
}

.job-form .bi-exclamation-triangle {
    color: #b61532;
    font-size: 18px;
}

.job-form .error-section {
    margin-top: 10px;
    text-align: justify;
}


//button
.job-form .my-button {
    position: relative;
    color: var(--main-blue);
    font-size: 16px;
    font-weight: 800;
    text-decoration: none;
    margin: 30px 0;
    border: 1px solid var(--main-blue);
    padding: 12px 30px;
    overflow: hidden;
    transition: .6s all ease;
    background: transparent;
    border-radius: 10px;
    z-index: 100000;
}

.job-form .my-button::before {
    background: var(--main-blue);
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    transition: all .6s ease;
}

.job-form .my-button:hover {
    color: #fff;
}

.job-form .my-button::before {
    width: 0%;
    height: 101%;
}

.job-form .my-button:hover::before {
    width: 100%;
}

////
.job-form .bv-no-focus-ring {
    text-align: justify;
    margin-top: 30px;
}

.job-form .custom-control-label::before {
    width: 25px;
    height: 25px;
    margin-left: -15px;
    margin-top: -9px;
}

.job-form .custom-control-label {
    margin-left: 15px;
    cursor: pointer
}

.job-form .custom-control-label::after {
    top: -6px;
    left: -40px;
    width: 26px;
    height: 26px;
}

.job-form .custom-control-input:checked~.custom-control-label::before {
    color: #fff;
    border-color: var(--main-blue);
    background-color: var(--main-blue);
    box-shadow: 0px 0px 6px #24487961;
}

//date

.job-form .b-form-btn-label-control.form-control {
    height: 50px;
}

.job-form .b-form-btn-label-control.form-control>.form-control {
    text-align: justify;
    margin-top: 5px
}

.job-form .bi-calendar,
.job-form .bi-calendar-fill {
    color: var(--main-blue)
}


///select

.job-form .my-select {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 2px;
    background-color: rgba(190, 190, 190, 0.0666666667);
    transition: all 0.5s ease;
    width: 100%;
    padding: 0px 10px;
    color: #495057;
}
</style>