<template>
    <section id="partner" class="partner">
        <div class="container">
            <h3 class="partner-title">
                Our Partners
            </h3>
            <div class="carousal">
                <carousel easing="linear" :paginationSize="12" :paginationPadding="15" paginationActiveColor="#244879"
                    :perPage="size"  :loop="true" :autoplay="true" :autoplayTimeout="1500">
                    <slide>
                        <img src="../assets/img/partner1.webp" class="partner-logo" alt="partner1" />
                    </slide>
                    <slide>
                        <img src="../assets/img/partner2.webp" class="partner-logo" alt="partner2"  />
                    </slide>
                    <slide>
                        <img src="../assets/img/partner3.webp" class="partner-logo" alt="partner3"  />
                    </slide>
                    <slide>
                        <img src="../assets/img/partner1.webp" class="partner-logo" alt="partner4"  />
                    </slide>
                    <!-- <slide>
                        <img src="../assets/img/partner2.webp" class="partner-logo" />
                    </slide>
                    <slide>
                        <img src="../assets/img/partner3.webp" class="partner-logo" />
                    </slide> -->
                </carousel>
            </div>
        </div>
    </section>
</template>

<script>


export default {
    name: "Partner",
    components: {

    },
    data() {
        return {
            screenWidth: 0,
            size: 0
        };
    },
    methods: {
        onScreenResize() {
            window.addEventListener("resize", () => {
                this.updateScreenWidth();
            });
        },
        updateScreenWidth() {
            this.screenWidth = window.innerWidth
        },
        setSize() {
            if (this.screenWidth >= '992')
                this.size = 3
            else
                this.size = 1
        },
    },
    created(){
        this.updateScreenWidth()
        this.onScreenResize()
        this.setSize()
        // console.log(this.size)
    }
};
</script>

<style lang="scss">
.partner {
    padding: 80px 0px;
    background-color: rgba(231, 231, 231, 0.356);
}

.partner .partner-title {
    text-align: center;
    font-size: 35px;
    font-weight: bold;
    color: var(--main-blue);
    margin-bottom: 60px;
}

.partner .VueCarousel-dot[data-v-438fd353]:focus {
    outline: 1px solid var(--main-blue);
}

.partner .VueCarousel-slide {
    padding: 10px
}

.partner .partner-logo {
    width: 70%;
    box-shadow: 0 0 10px #00000044;
}
</style>